/* --------- mixins --------- */
/* --------- responsive mixins --------- */
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
nav, output, ruby, section,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
html,
body {
  width: 100%;
  height: 100%;
  font-size: 14px;
}
input{
outline-color: #b12167;
}

body {
  font-family: 'GothamProLight';
  line-height: 1.6;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

ul {
  padding: 0;
  margin: 0;
}

ul li {
  padding: 0;
  margin: 0;
  list-style: none outside none;
}

button,
button:active,
button:focus {
  outline: none;
}

input::-webkit-input-placeholder {
  color: #7c6c69;
}

input::-moz-placeholder {
  color: #7c6c69;
}

/* Firefox 19 */

input:-moz-placeholder {
  color: #7c6c69;
}

/* Firefox 18 */

input:-ms-input-placeholder {
  color: #7c6c69;
}

p {
  padding: 0;
  margin: 0;
}
a {
  outline: none !important;
  text-decoration: none;
  color: white;
}

.main {
  position: relative;
  min-height: 100%;
  height: auto !important;
  height: 100%;
  width: 100%;
}
* {
  -webkit-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
}
img{
  -webkit-user-select: none !important;
  -moz-user-select: none !important;
  -ms-user-select: none !important;
  user-select: none !important;;
}
/* header */
.header{
  background-color: #27273e;
  min-height: 76px;
}
.box-top{
  margin: 0px auto;
  max-width: 1100px;
  display: flex;
}
.header .box{
  padding-top: 10px;
  justify-content: space-between;
}
.header__logo{
  width: 99px;
  height: 51px;
  background: url("/img/logo-fig.png") no-repeat;
  flex: 1 1 auto;
}
.header__contact{
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
}
.header__contacts-number{
  font-family: "GothamProBold";
  font-size: 21px;
  margin-left: auto;
}
.header__contacts-chart{
  margin-left: auto;
  color: #b12167;
  font-family: "GothamProBold";
  font-size: 14px;
  text-decoration: underline;
  text-decoration: 1px solid #b12167;
  height: 10px;
}
/* aside */
.wrsrcer{
  max-width: 1100px;
margin: 0px auto;

}
.flex__box{
  display: flex;
}
.pop-up__window{
// margin-left: 950px;
  position: absolute;
  width: 160px;
  height: 70px;
  background-color: white;
  font-size: 16px;
  font-weight: 600;
  color: #221f1f;
  padding: 10px;
  display: none;
}
.header__contacts-chart:hover .pop-up__window{
  display:block;
  margin: 30px -60px;
}
.pop-up__window p:first-child{
  color: #ec1e79;
}

.section__left{
  padding-top: 20px;
  width: 250px;
  min-height: 100%;
}
.section__left-pic{
  margin:20px;
}
.section__left-link{
  margin: 17px;
  font-size: 20px;
  color: #ec1e79;
}
.section__left-link:last-child{
  color: black;
}
.content_menu {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding-top: 20px;
  flex: 1 1 auto;
  /* padding-left: 15px; */
}
.content_menu-link {
  display: flex;
  align-items: center;
  text-decoration: none;
  margin: 15px;
}
.content_menu-link:hover > .content_menu-text {
  color: #000;
}
.content_menu-pic {
  width: 29px;
  height: 29px;
  margin-right: 14px;
  transition: .3s;
}
.content_menu-text {
  font-weight: 700;
  font-size: 14px;
  color:  rgb(255, 0, 106);
  transition: .3s;
}
/* bread-crumd */
.section__right{
  display: flex;
  flex-direction: column;
  padding-top: 20px;
  margin-left: 40px;

}
.breadcrumbs{
  padding-top: 30px;
  max-height: 60px;
}
.breadcrumbs__item{
  font-size: 16px;
  color: #ec1e79;
}
.breadcremd__text{
  font-size: 16px;
  color: black;
}
.breadcrumbs__item:last-child{
color: black;
}
.heading{
  font-weight: 900;
  display: block;
  font-size: 50px;
  font-family: "GothamProBold";
}
// cards
.cards{
