// footer
  .footer__fix{
    margin: 0px auto;
    max-width: 1100px;
    display: flex;
  }
  .footer{
    min-height: 77px;
    background: #27273e;
    clear: both;
    }
    .footer .box{
      display: block;
    } 
    .footer__left span{
    font-weight: 700;
    }
    .footer__span{
    border-bottom: 1px solid #5d5d6e ; 
